/* Common stylesheets */
.base-font-style, .regular-font-style, .bold-font-style, .h1, h1, .h2, h2, .h3, h3, .card-title, .p, input, body, html {
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase; }

.regular-font-style {
  font-weight: 400; }

.bold-font-style, .h1, h1, .h2, h2, .h3, h3, .card-title {
  font-weight: 700; }

.h1, h1 {
  font-size: 1.95rem; }

.h2, h2 {
  font-size: 1.56rem; }

.h3, h3, .card-title {
  font-size: 1.25rem; }

body, html {
  font-size: 3.1vw; }
  @media (min-width: 1008px) {
    body, html {
      font-size: 1.13vw; } }

